@layer content {
    .contentarea {
        --radius: 4px;

        font-family: var(--cnt-font);
        font-size: var(--cnt-font-size);
        color: var(--cnt-color);
        line-height: 1.6em;
    }

    .contentarea a {
        color: var(--cnt-link-color);
        text-decoration: none;
    }

    .contentarea a:visited {
        color: var(--cnt-link-visited-color);
    }

    .contentarea a:hover {
        color: var(--cnt-link-hover-color);
    }

    .contentarea a:active {
        color: var(--cnt-link-active-color);
    }

    .contentarea a:focus {
        outline: thin dotted;
    }

    .contentarea a:hover,
    .contentarea a:active {
        outline: 0;
    }

    .contentarea p {
        margin: 1rem 0;
        margin-block-start: 0.5rem;
        -ms-hyphens: auto;
        -moz-hyphens: auto;
        -webkit-hyphens: auto;
        hyphens: auto;
    }

    .contentarea p code,
    .contentarea ul code,
    .contentarea ol code {
        color: var(--cnt-header-color);
        background-color: var(--cnt-border-color);
        padding: 3px 4px;
        border-radius: var(--radius);
    }

    .contentarea img {
        max-width: 100%;
        float: right;
        margin: 0 0 1rem 1rem;
    }

    .contentarea h1,
    .contentarea h2,
    .contentarea h3,
    .contentarea h4,
    .contentarea h5,
    .contentarea h6 {
        font-family: var(--cnt-header-font);
        font-weight: normal;
        color: var(--cnt-header-color);
        line-height: 1em;
        margin-block: 0;
        padding-inline: 0;
        clear: both;
    }

    .contentarea h1 {
        margin: 1rem 0 2rem 0;
        font-size: 2rem;
        font-weight: bold;
        border-bottom: 0.1em var(--cnt-border-color) solid;
    }

    .contentarea h2 {
        font-size: 1.6rem;
    }

    .contentarea h3 {
        font-size: 1.4rem;
    }

    .contentarea h2,
    .contentarea h3 {
        margin: 2.5rem 0 0 0;
        border-bottom: 0.1em var(--cnt-border-color) solid;
    }

    .contentarea blockquote {
        font-style: italic;
        font-size: 1rem;
        margin: 1rem;
        text-align: justify;
        padding: 1rem 2rem;
        box-shadow: 4px 4px 0 var(--cnt-dark-border);
        background-color: var(--col-prim);
        max-width: 90%;
    }

    .contentarea blockquote:before {
        display: block;
        padding-left: 10px;
        content: "“";
        font-size: 3rem;
        position: relative;
        left: -2.5rem;
        top: 0.5rem;
        height: 0;
    }

    .contentarea hr {
        display: block;
        height: 2px;
        border: 0;
        border-top: 1px solid #aaa;
        border-bottom: 1px solid #eee;
        margin: 1em 0;
        padding: 0;
    }

    .contentarea code,
    .contentarea pre {
        font-family: var(--mono-font);
        font-size: var(--cnt-mono-font-size);
        line-height: 1.5em;
        tab-size: 4;
        hyphens: none;
    }

    .contentarea pre:not(.syntaxhighlight) {
        background-color: var(--cnt-pre-color);
    }

    .contentarea pre {
        padding: 1em;
        border-radius: var(--radius);
        overflow-x: auto;
        overflow-y: hidden;
        clear: both;
    }

    .contentarea pre.console {
        background-color: black;
        color: lightgray;
    }

    .contentarea pre.console:before {
        content: '> ';
        color: darkgray;
    }

    .contentarea b,
    .contentarea strong {
        font-weight: bold;
    }

    .contentarea ins {
        background: #ff9;
        color: #000;
        text-decoration: none;
    }

    .contentarea mark {
        background: #ff0;
        color: #000;
        font-style: italic;
        font-weight: bold;
    }

    .contentarea sub,
    .contentarea sup {
        font-size: 75%;
        line-height: 0;
        position: relative;
        vertical-align: baseline;
    }

    .contentarea sup {
        top: -0.5em;
    }

    .contentarea sub {
        bottom: -0.25em;
    }

    .contentarea ul,
    .contentarea ol {
        margin: 1em 0;
        padding: 0 0 0 2em;
    }

    .contentarea p+ul,
    .contentarea p+ol {
        margin-top: -0.5em;
    }

    .contentarea li {
        padding-left: 0.5em;
    }

    .contentarea dd {
        margin: 0 0 0 2em;
    }

    .contentarea table {
        border-collapse: collapse;
        border: 1px solid var(--col-prim-dark);
        border-radius: 0.5em;
        margin-inline: auto;
        overflow: hidden;
        color: var(--col-prim-text);
        padding: 1em;
    }

    .contentarea tbody tr:nth-child(odd) {
        background-color: var(--col-prim-dark);
    }
    .contentarea tbody tr:nth-child(even) {
        background-color: var(--col-prim);
    }
    .contentarea th,
    .contentarea td {
        padding: 0.5em 1em; 
    }

    .contentarea thead {
        background-color: var(--col-sec-light);
        color: var(--col-sec-text-light);
        text-align: left;
        font-family: var(--sans-font);
    }

    .contentarea td {
        vertical-align: top;
    }

    .contentarea svg {
        display: block;
        margin-left: auto;
        margin-right: auto;
    }

    .contentarea summary {
        font-family: var(--cnt-header-font);
        font-weight: normal;
        color: #888;
        cursor: pointer;
    }

    .contentarea summary:focus {
        outline: none;
    }

    .contentarea summary:hover {
        color: var(--cnt-color);
    }

    .contentarea details {
        padding: 0px 8px;
        border: #AAA dashed 1px;
        border-radius: var(--radius);
        max-height: 2em;
        overflow: auto;
        transition: max-height 2s;
    }

    .contentarea details[open] {
        max-height: 95vh;
    }

    .contentarea .alert {
        font-family: var(--sans-font);
        font-weight: 600;
        display: flex;
        align-items: center;
        border-radius: var(--radius);
        padding: 1em;
        background-color: var(--bgColor, #f8d7da);
        color: var(--textColor, #721c24);
    }

    .contentarea .alert::before {
        content: var(--icon);
        font-size: 24px;
        margin-right: 0.5em;
    }

    .contentarea .alert.error {
        --bgColor: #f8d7da;
        --textColor: #721c24;
        --icon: "⛔";
    }

    .contentarea .alert.warning {
        --bgColor: #fff3cd;
        --textColor: #856404;
        --icon: "⚠️";
    }

    .contentarea .alert.info {
        --bgColor: #d1ecf1;
        --textColor: #0c5460;
        --icon: "ℹ️";
    }

    .contentarea .alert.success {
        --bgColor: #d4edda;
        --textColor: #155724;
        --icon: "✅";
    }

    @media only screen and (max-width: 850px) {
        .contentarea img {
            float: none;
            display: block;
            max-width: 90%;
            margin: 1rem auto;
        }
    }
}